import { useRouter } from 'next/router';
import { useEffect, useState } from 'react';

import { Typography } from 'src/components/dataDisplay';
import { ThinRectangleBox } from 'src/components/layout';
import { LinkButton } from 'src/components/navigation';
import { BestFitProductLoadingPaper } from 'src/components/papers';
import BestFitProductResultPaper from 'src/components/papers/BestFitProductResultPaper';
import { BASE_PATH } from 'src/constants/resouce';

const removeFirstSlash = (path: string) => {
  if (!path || !path.length) {
    return path;
  }
  const first = path.substring(0, 1);
  if (first !== '/') {
    return path;
  }
  return path.substring(1);
};

const BestFitProductResult = (): JSX.Element => {
  const [isLoading, setIsLoading] = useState(true);
  const router = useRouter();
  const { pathname = '', basePath = '', isReady, query } = router;
  const productId = query.id;

  useEffect(() => {
    if (productId) {
      setTimeout(() => setIsLoading(false), 3000);
    }
  }, [productId]);

  if (!isLoading && !productId) {
    // TODO: 에러 페이지는 따로 정리해야 한다.
    const pathnames = pathname.split('/');
    pathnames.pop(); // Remove the last element
    const parentPathname = removeFirstSlash(pathnames.join('/'));
    const homeHref = `${BASE_PATH}${basePath}${parentPathname}`;
    return (
      <ThinRectangleBox>
        <div>
          <Typography variant="body">올바르지 않은 접근 입니다.</Typography>
        </div>
        <LinkButton href={homeHref}>내게 맞는 제품 홈</LinkButton>
      </ThinRectangleBox>
    );
  }

  if (!isReady || isLoading) {
    return (
      <ThinRectangleBox>
        <BestFitProductLoadingPaper />
      </ThinRectangleBox>
    );
  }

  return (
    <ThinRectangleBox>
      <BestFitProductResultPaper />
    </ThinRectangleBox>
  );
};

export default BestFitProductResult;
